import {
    Badge,
    Card,
    CardHeader,
    CardFooter,
    DropdownMenu,
    DropdownItem,
    UncontrolledDropdown,
    DropdownToggle,
    Media,
    Pagination,
    PaginationItem,
    PaginationLink,
    Progress,
    Table,
    Container,
    Row,
    UncontrolledTooltip,
  } from "reactstrap";

import { AppIcons } from "../../components/ApiCall/AppIcons";

const EarningTable = (props) => {

    const currencyStandard = (number) => {
        if(number === undefined || number === null){
            return "0.00"
        }
        return number.toFixed(2)
    }

    return (
        <>
            <Table className="align-items-center table-flush" responsive>
                <thead className="thead-light">
                  <tr>
                    <th scope="col">App</th>
                    <th scope="col">Total Earning</th>
                    <th scope="col">Today's Earning</th>
                    <th scope="col">Yesterday's Earning</th>
                    {/* <th scope="col">This Week's Earning</th>
                    <th scope="col">This Month's Earning</th>
                    <th scope="col">Last Month's Earning</th> */}
                  </tr>
                </thead>
                <tbody>
                    {AppIcons()?.map((app) =>(
                        <tr>
                        <th scope="row">
                          <Media className="align-items-center">
                            <img
                                alt={app.app}
                                src={app.logo}
                                width={50}
                            />
                            <Media>
                              <span className="mb-0 text-sm">
                                {app.name}
                              </span>
                            </Media>
                          </Media>
                        </th>
                        <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["total"] || 0)}
                        </td>
                        <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["today"] || 0)}
                        </td>
                        <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["yesterday"] || 0)}
                        </td>
                        {/* <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["this_week"])}
                        </td>
                        <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["this_month"])}
                        </td>
                        <td>
                            &#x20b9; {currencyStandard(props.account[app.app]["last_month"])}
                        </td> */}
                      </tr>
                    ))}
                  
                </tbody>
              </Table>
        </>
    )
}
export default EarningTable