import { useState, useEffect } from "react";
import Header from "components/Headers/NewHeader.js";
import { UserAccountFullDetails } from "../../components/ApiCall/AccountApi";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import UserAppChart from "./UserAppChart";

const NewLanding = (props) => {
    const userInfo = useSelector((state)=>state.user.userData);
    const tokenData = useSelector((state)=>state.user.user);
    const [userAccount, setUserAccount] = useState({})


    useEffect(() => {
        UserAccountFullDetails(userInfo.userId, tokenData.token).then((res)=>{
            if(res.status == 200){
                setUserAccount(res.data)
            }else{
                toast.error(res.data.message,{
                    position:toast.POSITION.TOP_CENTER
                });
            }
        }).catch((err)=>{
            toast.error(err.message,{
                position:toast.POSITION.TOP_CENTER
            });
        })
    },[])

    const currencyStandard = (number) => {
        console.log(number)
        if(number === undefined || number === null){
            return "0.00"
        }
        return number.toFixed(2)
    }

    return (
        <div className="new-landing">
            <div className="landing-body ">
                <div className="row no-gutters total-estimate-earnings">
                    <div className="col-lg-12 col-xl-12 col-md-12">
                        <h3 className="text-white">Total estimated earnings<span className="quest-mark">?</span></h3>
                        <div className="row no-gutters">

                            <div className="col-lg-6 col-xl-6 col-md-6 col-sm-12">
                                <div className="row no-gutters justify-content-between">
                                    <div className="col-lg-6 mb-view">
                                        <p className="text-muted"> Total Earnings</p>
                                        <h1 className="text-white">&#x20b9; {currencyStandard(userAccount.total)}</h1>
                                    </div>
                                    <div className="col-lg-6 mb-view">
                                        <p className="text-muted">Today's Earning</p>
                                        <h1 className="text-white">&#x20b9;{currencyStandard(userAccount.today)}</h1>
                                    </div>
                                </div>
                            </div>

                            <div className="col-lg-6 col-xl-6 col-md-6 col-sm-12">
                                <div className="row no-gutters justify-content-between">
                                    <div className="col-lg-6 mb-view">
                                        <p className="text-muted">This month so far</p>
                                        <h1 className="text-white">&#x20b9;{currencyStandard(userAccount.this_month)}</h1>
                                    </div>
                                    <div className="col-lg-6 mb-view">
                                        <p className="text-muted">Last month</p>
                                        <h1 className="text-white">&#x20b9;{currencyStandard(userAccount.last_month)}</h1>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>

                <div className="row no-gutters pt-4 pb-4">
                    <div className="col-lg-4">Graphical View</div>
                    <div className="col"></div>
                </div>

                <div className="row no-gutters justify-content-between mt-4 ads-app-performance">
                    <div className="col-xl-12 col-lg-12 col-md-12">
                        {/* <h3>Ads activity performance</h3>
                        <div className="row no-gutters">
                            <div className="col-sm-6 mb-view">
                                <p>Est. earnings<span className="quest-mark">?</span></p>
                                <h1>&#x20b9;0.00</h1>
                            </div>
                            <div className="col-sm-6 mb-view">
                                <p>Requests<span className="quest-mark">?</span></p>
                                <h1>650</h1>
                            </div>

                            <div className="col-sm-6 mb-view">
                                <p>Impr.<span className="quest-mark">?</span></p>
                                <h1>1</h1>
                            </div>
                            <div className="col-sm-6 mb-view">
                                <p>Match rate<span className="quest-mark">?</span></p>
                                <h1>0.92%</h1>
                            </div>
                            <div className="col-sm-6 mb-view">
                                <p>eCPM<span className="quest-mark">?</span></p>
                                <h1>&#x20b9;0.00</h1>
                            </div>
                            <div className="col-sm-6"></div>
                        </div> */}
                        <UserAppChart accountInfo={userAccount} />

                    </div>
                    {/* <div className="col-xl-4 col-lg-4 col-md-12 card app-performance">
                        <h3>App performance</h3>
                        <div className="row no-gutters">
                            <div className="col-sm-7">
                                <select name="cars" id="cars">
                                    <option value="volvo">Volvo</option>
                                    <option value="saab">Saab</option>
                                    <option value="mercedes">Mercedes</option>
                                    <option value="audi">Audi</option>
                                </select>
                            </div>
                            <div className="col-sm-6"></div>
                        </div>
                        <table className="table">
                            <thead className="table-borderless">
                                <tr>
                                    <th scope="col">App</th>
                                    <th scope="col">Est. earnings</th>
                                    <th scope="col">Impr.</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>
                                        <div className="row no-gutters">
                                            <div className="col-sm-6">
                                            <img src={require("../../assets/img/icons/app.PNG")} alt="image" />
                                            </div>
                                            <div className="col">
                                                <div className="row no-gutters">
                                                    <div className="col-12">
                                                    teams builder
                                                    </div>
                                                    <div className="col-12">Android</div>
                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                    <td>
                                        <span>&#x20b9;0.00</span>
                                        <div>&#x20b9;0.00(-)</div>
                                    </td>
                                    <td>
                                        <span>1</span>
                                        <div>-1 (-50.00%)</div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div> */}
                </div>

            </div>
        </div>
    )
}

export default NewLanding;