import {
    Container,
    Jumbotron,
    Button,
    Row,
    Card,
    Col
} from "reactstrap";
import { Link } from 'react-router-dom';
import React, { useState, useEffect } from "react";
import HomeNavbar from "../../components/Headers/HomeHeader";
import Footer from "../../components/Footers/HomeFooter";
import { useHistory } from "react-router-dom";
import { AppIcons } from "../../components/ApiCall/AppIcons";

const Home = () => {
    const history = useHistory()
    const appNames = AppIcons();

    const [appData, setAppData] = useState({
        appName: appNames[0].name,
        appDetails: appNames[0].desc,
        appLogo: appNames[0].logo
    });


    useEffect(()=>{
        getAppName();
    },[])

    const getAppName = () => {
        let i = 0;
       setInterval(()=>{
            if(i >= appNames.length){
                i =0;
            }
            setAppData({
                ...appData,
                appName: appNames[i].name,
                appDetails: appNames[i].desc,
                appLogo: appNames[i].logo,
                appLink: appNames[i].link
             })
           i++;
       },2000)
    }

    

    const loginRedirect = () => {
        history.push('/auth/login');
    }

    const signupRedirect = () => {
        history.push('/auth/register');
    }

    return (
        <div className="home">
            <HomeNavbar activate={true} />
            <Jumbotron className=" main-home-section-top">
                <Container>
                    <Row>
                        <div className="offset-lg-6 col-lg-6 text-md-center">
                            <h1 className="cw-header-h1">Let's build your team</h1>
                        </div>
                        <div className="offset-lg-6 col-lg-6">
                            <Card className="light bigCornerRadius jumboCard" style={{   }}>
                                <p className="lead">Teams Builders always looking for mobile internet users from India.</p>
                                <p className="lead">How it works: You can sign up for free here. No charges. </p> 
                                <Row>
                                    <Button className="mr-2" color="primary" onClick={(e) => {e.preventDefault();loginRedirect()}}>Login</Button>
                                    <Button className="mr-2" color="info"  onClick={(e) => {e.preventDefault();signupRedirect()}}>SignUp</Button>
                                </Row>
                            </Card>
                        </div>
                    </Row>
                </Container>
            </Jumbotron>
            <section className="section-space">
                <Container className="pt-2">
                    <Row>
                        <Col className="col-md-12 col-lg-6 col-xl-6 text-center">
                            <h1 className="heading-top-view">{appData.appName}</h1>
                            <img src={appData.appLogo} style={{width:'150px'}} alt="logo" />
                        </Col>
                        <Col className="col-md-12 col-lg-6 col-xl-6 text-md-center team-app-ads-section">
                            <div>
                                <h1 className="heading-top-view">{appData.appName}</h1>
                                <p> {appData.appDetails}
                                    <Link className="text-info font-weight-bold" to="#">Learn more</Link>
                                </p>
                                <Row>
                                    <Col>
                                        {/* <a href="#" target="_blank" className="iosAppBtn en" rel="noopener noreferrer">Download Clickworker App from AppStore</a> */}
                                        <a href={appData.appLink} target="_blank" className="andAppBtn en" rel="noopener noreferrer">Download Teamsbuilders App from PlayStore</a>
                                    </Col>
                                </Row>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
            <section className="gray section-space">
                <Container className="pt-2 card-section">
                    <Row>
                        <Col className="col-lg-8 card-section-head">
                            <h2 className="h2-text">How you will start your work?</h2>
                            <p>Here you can get a brief overview about your work.</p>
                        </Col>
                    </Row>
                    <Row>
                        <Col className="col-lg-4 col-xl-4 col-md-12 pb-md-4 col-sm-12">
                            <Card className="text-center bigCornerRadius jumboCard info-card">
                                <figure>
                                    <img 
                                    alt="video.."
                                    width="100"
                                    src="https://cdn.imgbin.com/11/9/18/imgbin-video-clip-computer-icons-video-on-demand-atm-s-ACUvUHuzCLPx358W8DEk8pz1w.jpg"
                                    />
                                </figure>
                                
                                <h4>How to sign up and sign in video.</h4>
                                <p>coming soon.</p>
                                {/* <p>
                                    <a href="https://www.clickworker.com/text-create-jobs/?customer=false" className ="arrow">More about Text Creation</a>
                                </p> */}
                            </Card>
                        </Col>
                        <Col className="col-lg-4 col-xl-4 col-md-12 pb-md-4 col-sm-12">
                            <Card className="text-center bigCornerRadius jumboCard info-card">
                                <figure>
                                    <img 
                                    alt="video.."
                                    width="100"
                                    src="https://cdn.imgbin.com/11/9/18/imgbin-video-clip-computer-icons-video-on-demand-atm-s-ACUvUHuzCLPx358W8DEk8pz1w.jpg"
                                    />
                                </figure>
                                
                                <h4>How to activate your account video.</h4>
                                <p>coming soon.</p>
                                {/* <p>
                                    <a href="https://www.clickworker.com/text-create-jobs/?customer=false" className ="arrow">More about Text Creation</a>
                                </p> */}
                            </Card>
                        </Col>
                        <Col className="col-lg-4 col-xl-4 col-md-12 col-sm-12">
                            <Card className="text-center bigCornerRadius jumboCard info-card">
                                <figure>
                                    <img 
                                    alt="video.."
                                    width="100"
                                    src="https://cdn.imgbin.com/11/9/18/imgbin-video-clip-computer-icons-video-on-demand-atm-s-ACUvUHuzCLPx358W8DEk8pz1w.jpg"
                                    />
                                </figure>
                                
                                <h4>How to refer your friend and referral bonus video.</h4>
                                <p>coming soon.</p>
                                {/* <p>
                                    <a href="https://www.clickworker.com/text-create-jobs/?customer=false" className ="arrow">More about Text Creation</a>
                                </p> */}
                            </Card>
                        </Col>
                        <Col className="col-lg-4 col-xl-4 col-md-12 col-sm-12">
                            <Card className="text-center bigCornerRadius jumboCard info-card">
                                <figure>
                                    <img 
                                    alt="video.."
                                    width="100"
                                    src="https://cdn.imgbin.com/11/9/18/imgbin-video-clip-computer-icons-video-on-demand-atm-s-ACUvUHuzCLPx358W8DEk8pz1w.jpg"
                                    />
                                </figure>
                                
                                <h4>How to setup payment account video.</h4>
                                <p>coming soon.</p>
                                {/* <p>
                                    <a href="https://www.clickworker.com/text-create-jobs/?customer=false" className ="arrow">More about Text Creation</a>
                                </p> */}
                            </Card>
                        </Col>
                        <Col className="col-lg-4 col-xl-4 col-md-12 col-sm-12">
                            <Card className="text-center bigCornerRadius jumboCard info-card">
                                <figure>
                                    <img 
                                    alt="video.."
                                    width="100"
                                    src="https://cdn.imgbin.com/11/9/18/imgbin-video-clip-computer-icons-video-on-demand-atm-s-ACUvUHuzCLPx358W8DEk8pz1w.jpg"
                                    />
                                </figure>
                                
                                <h4>How to generate money video.</h4>
                                <p>coming soon.</p>
                                {/* <p>
                                    <a href="https://www.clickworker.com/text-create-jobs/?customer=false" className ="arrow">More about Text Creation</a>
                                </p> */}
                            </Card>
                        </Col>
                    </Row>
                </Container>
            </section>
            <section className="section-space bg-white">
                <Container className="px-lg-7">
                    <div className="row no-gutters">
                        <div className="col-lg-6 col-xl-6 col-md-6 col-sm-12">
                            <h2 className="h2-text">This is how you will work</h2>
                            <ul className="checkmarks"> 
                                <li>Sign up at teamsbuilders.com</li>
                                <li>Fill out your user profile</li> 
                                <li>Complete activation process</li> 
                                <li>Process your works online</li> 
                                <li>Get paid on monthly basis</li>
                            </ul>
                        </div>
                        <div className="col-lg-6 col-xl-6 col-md-6 col-sm-12">
                            <h2 className="h2-text">Your benefits at glance</h2>
                            <ul className ="checkmarks">
                                <li>Easy start with assessments</li> 
                                <li>Good quality work ensures an increase in earnings</li> 
                                <li>Work from home whenever you have time</li> 
                                <li>Monthly payment via wire transfer or UPI or Paytm Wallet</li>  
                                <li>24/7 support from our teamsbuilders community</li>
                            </ul>
                        </div>
                    </div>
                </Container>
            </section>
            <Footer />
        </div>
    )

}
export default Home