
import { Card, CardBody, CardHeader, CardTitle, Container, Row, Col, Button } from "reactstrap";
import Activity from './Activity';


const AppInfo = (props) => {

    const imageStyle =  {
        height: '90px'
    }

    const sectionStyle = {
        width: '800px'
    }

    return (
        <>
        <Card className="app-info">
            <CardHeader className="pb-0">
                <CardTitle className="h3 text-center">{props.app.name} 
                    <span className="pl-2">
                    {
                            props.activation ? <>
                                {
                                    props.activation[props.app.id] ? 
                                    <img src={require("../../assets/img/icons/active-state.png")} className="state-logo" alt="state-logo" />
                                    : 
                                    <img src={require("../../assets/img/icons/inactive-state.png")} className="state-logo" alt="state-logo" />
                                }
                            </> :
                            <>
                                <img src={require("../../assets/img/icons/inactive-state.png")} className="state-logo" alt="state-logo" />
                            </> 
                        }
                    </span>
                </CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md={2} className="image-main-section">
                        <img src={props.app.logo} className=" img-style" style={imageStyle} alt="" />
                    </Col>
                    <Col md={10} className="info-content-main">
                    <ol className="info-content">
                        <li className="font-weight-bold">Download and Install {props.app.name} Application from Playstore. </li>
                        <li>create 10 no of bills which containing min 50 products (10 products from each category and the first product and last product must be present)</li>
                        <li>The categories are <span className="imp-category">{props.app.categories}</span>.</li>
                        <li>10 bills are divided in three days, first day you can create 4 bills, second day 3 bills and in third day with 3 bills.</li>
                        <li>for more details checkout the video link <a href="#">Yet to be uploaded</a>.</li>
                    </ol>
                    <div className="text-center">
                        <Activity app={props.app} user={props.user}/>
                    </div>
                    </Col>
                    {/* <Col md={1} className="text-right">
                        {
                            props.activation ? <>
                                {
                                    props.activation[props.app.id] ? 
                                    <img src={require("../../assets/img/icons/active-state.png")} className="state-logo" alt="" />
                                    : 
                                    <img src={require("../../assets/img/icons/inactive-state.png")} className="state-logo" alt="" />
                                }
                            </> :
                            <>
                                <img src={require("../../assets/img/icons/inactive-state.png")} className="state-logo" alt="" />
                            </> 
                        }
                        
                    </Col> */}
                </Row>
            </CardBody>
        </Card>
    </>
    )
}

export default AppInfo;