// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
} from "reactstrap";
import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {login} from '../../reducers/userReducer';
import { userLogin, userDetail } from '../../components/ApiCall/userApi';
import { toast } from "react-toastify";

const Login = (props) => {
  let history = useHistory()
  const [loginData, setLoginData] = useState({
    mob: "",
    password: "",
    count: ""
  });

  const [rememberme, setRememberMe] = useState(false);
  // const [isAuth, userLoginAuth] = UserAuth(false)
  const dispatch = useDispatch()
  useEffect(() => {
    getRememberData();
    // Update the document title using the browser API
    // document.title = `You clicked ${loginData.count} times`;
    // console.log(process.env.REACT_APP_TEAMSBUILDERS_URL)
  },[]);

  const getRememberData = () => {
    try{
      const userCreds = JSON.parse(localStorage.getItem("my_tb_creds"));
      if(userCreds){
        setLoginData((prevValue=>{
          return {
            mob: userCreds.mob, password: userCreds.password, count: ""
          }
      }))
      setRememberMe(true)
     }
    }catch(e){
      console.error(e)
    }
  }

  const loginFunc = async (e) => {
    e.preventDefault();
    e.target.login_btn.innerHTML = "Signing in...";
    e.target.login_btn.disabled = true;
    let mob = e.target.mob.value;
    let password = e.target.password.value;
    rememberMe(rememberme, mob, password);
    let res = await userLogin(mob, password);
    if(res.status === 200){
      toast.success(res.data.message,{
        position:toast.POSITION.TOP_CENTER
      });
      let userData = await userDetail(res.data.user_id, res.data.token);
      if(userData.status === 401){
        alert("user is not authorized")
        return;
      }
      sessionStorage.setItem("isAuth", true);
      sessionStorage.setItem("userData", JSON.stringify(res.data));

      dispatch(
        login(
          { 
            user: res.data,
            userData: userData.data.data,
            isLoggedIn: true
          }
        )
      )
      setTimeout(() =>{
       history.push('/admin/index')
      },1000)
    }else{
      e.target.login_btn.innerHTML = "Sign in";
      e.target.login_btn.disabled = false;
      toast.error(res.data.message, {
        position: toast.POSITION.TOP_CENTER
      });
    }
  }

  const inputEvent = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setLoginData((prevValue)=>{
      if(name === "mob"){
        return {
          mob: value,
          password: prevValue.password
        }
      }else{
        return {
          password: value,
          mob: prevValue.mob
        }
      }
    })
  }

const eyeSplashStyle = {
  cursor: "pointer"
}

const togglePassword = (e) => {
  let password = document.getElementById("password");
  if(e.target.className === "fa fa-eye" && password.type === "password"){
    e.target.className = "fa fa-eye-slash"
    password.type = "text";
  }else{
    e.target.className = "fa fa-eye";
    password.type = "password";
  }
}

const checkRememberMe = (e) => {
  if(e.target.checked){
    setRememberMe(true);
  }else{
    setRememberMe(false);
  }
}

const rememberMe = (check, mob, password) => {
  if(check){
    localStorage.setItem("my_tb_creds", JSON.stringify({"mob":mob, "password":password}))
  }else{
    localStorage.removeItem("my_tb_creds");
  }
}

  return (
    <>
      <Col lg="5" md="5">
        <Card className="bg-secondary shadow border-0">
          <CardBody className="px-lg-5 py-lg-3">
            <div className="text-center text-muted mb-4">
              <small>sign in with credentials</small>
            </div>
            <Form role="form" method="post" onSubmit={loginFunc}>
              <FormGroup className="mb-3">
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-mobile-button" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Mob"
                    type="text"
                    name="mob"
                    onChange={inputEvent}
                    autoComplete="new-mob"
                    value={loginData.mob}
                    required
                    maxLength="10"
                    size="10"
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-lock-circle-open" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Password"
                    type="password"
                    name="password"
                    id="password"
                    onChange={inputEvent}
                    autoComplete="new-password"
                    value={loginData.password}
                    required
                  />
                  <InputGroupText>
                    <i className="fa fa-eye" onClick={togglePassword} style={eyeSplashStyle} aria-hidden="true"></i>
                  </InputGroupText>
                </InputGroup>
              </FormGroup>
              <div className="custom-control custom-control-alternative custom-checkbox">
                <input
                  className="custom-control-input"
                  id=" customCheckLogin"
                  type="checkbox"
                  onChange={checkRememberMe}
                  checked={rememberme}
                />
                <label
                  className="custom-control-label"
                  htmlFor=" customCheckLogin"
                >
                  <span className="text-muted">Remember me</span>
                </label>
              </div>
              <div className="text-center">
                <Button className="my-4" name="login_btn" color="primary" type="submit">
                  Sign in
                </Button>
              </div>
            </Form>
          </CardBody>
        </Card>
        <Row className="mt-3">
          <Col xs="6">
            <a
              className="text-light"
              href="#pablo"
              onClick={(e) => {e.preventDefault(); history.push('/auth/forgot-password');}}
            >
              <small>Forgot password?</small>
            </a>
          </Col>
          <Col className="text-right" xs="6">
            <a
              className="text-light"
              href="#pablo"
              onClick={(e) => {e.preventDefault(); history.push('/auth/register')}}
            >
              <small>Create new account</small>
            </a>
          </Col>
        </Row>
      </Col>
    </>
  );
};






export default Login;
