import { createSlice } from '@reduxjs/toolkit'

export const existUserSlice  = createSlice({
    name: "existUserReducer",
    initialState: {
        user_id: null,
        userData: null,
        userToken: null,
        preRegister: false,
    },
    reducers: {
        existance: (state, action) => {
            state.user_id = action.payload.user_id;
            state.userData = action.payload.user_data;
            state.userToken = action.payload.user_token;
            state.preRegister = action.payload.pre_register;
        }
    }
})

export const { existance } = existUserSlice.actions;

export const selectExistUserData = (state) => state.userData;

export default existUserSlice.reducer;