import axios from "axios";

export const UserAccountFullDetails = async (user_id, token) => {
    if(!user_id)return;
    var config = {
        method: 'get',
        url: process.env.REACT_APP_TEAMSBUILDERS_URL + `api/user/account/details?appname=allapps&userId=${user_id}&fullDetails=ok`,
        headers: { 
          'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI',
          'Authorization': `token ${token}`, 
        }
      };
      try{
        return await axios(config);
      }catch(error){
          return error.response
      }
}

export const AddUserBankAccount = async (user_id, acc_num, ifsc_code, acc_holder, bank_name,token) => {
    if(!user_id || !ifsc_code || !acc_num || !acc_holder || !bank_name)return;
    var data = JSON.stringify({
        "user": user_id,
        "account_number": acc_num,
        "ifsc_code": ifsc_code,
        "account_holder_name": acc_holder,
        "bank_name":bank_name
      });
      
    var config = {
        method: 'post',
        url: process.env.REACT_APP_TEAMSBUILDERS_URL+'api/bank-detail/',
        headers: { 
            'Content-Type': 'application/json', 
            'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI',
            'Authorization': `token ${token}`, 
        },
        data : data
    };
    try{
        return await axios(config);
    }catch(error){
        return error.response
    }
}


