export const PostUserActiveAccount = async (type,data_id,token) => {
    // bankDetails=${bankDetails}&upi=${upi}&paytm=${paytm}
    var data = JSON.stringify({
        "paytm_wallet_id": data_id,
        "bank_account_id": data_id,
        "upi_id": data_id
    });
      
    var config = {
        method: 'post',
        url: process.env.REACT_APP_TEAMSBUILDERS_URL+`api/active/beedf46c-9ecf-4770-8b77-bcc25d532ad8/payment-details?${type}=True`,
        headers: { 
            'Content-Type': 'application/json', 
            'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI',
            'Authorization': `token ${token}`, 
        },
    data : data
    };

    try{
        return await axios(config);
    }catch(error){
        return error.response
    }
}