import { createSlice } from '@reduxjs/toolkit'

export const EventEmitterSlice = createSlice({
    name: "EventEmitterReducer",
    initialState: {
        listening: false,
    },
    reducers: {
        checkEventListener: (state, action) => {
            state.listening = action.payload.listening;
        }
    }
})


export const { checkEventListener } = EventEmitterSlice.actions;
export const selectEventEmitter =  (state) => state.listening;
export default EventEmitterSlice.reducer;
