
import { useState, useEffect } from "react";
// node.js library that concatenates classes (strings)
import classnames from "classnames";
// javascipt plugin for creating charts
import Chart from "chart.js";
// react plugin used to create charts
import { Line, Bar } from "react-chartjs-2";

import {
    Button,
    Card,
    CardHeader,
    CardBody,
    NavItem,
    NavLink,
    Nav,
    Progress,
    Table,
    Container,
    Row,
    Col,
    Input
  } from "reactstrap";
  
import {
    chartOptions,
    parseOptions,
    chartExample1,
    chartExample2,
    chartExample4,
    chartExample5
} from "variables/charts.js";

const UserAppChart = (props) => {
    const [activeNav, setActiveNav] = useState(1);
    const [chartExample1Data, setChartExample1Data] = useState("data1");
    const [accountInfo, setAccountInfo] = useState(null)
    const [accountType, setAccountType] = useState("1")

    useEffect(()=>{
        setAccountInfo(props.accountInfo)
    },[props])

    const getData = (day, app) => {
        switch (day) {
            case "1":
                try{
                    return accountInfo[app]["total"]
                }catch(e){
                    return 0
                }
            case "2":
                try{
                    return accountInfo[app]["today"]
                }catch(e){
                    return 0
                }

            case "3":
                try{
                    return accountInfo[app]["yesterday"]
                }catch(e){
                    return 0
                }
            case "4":
                try{
                    return accountInfo[app]["this_week"]
                }catch(e){
                    return 0
                }
            case "5":
                try{
                    return accountInfo[app]["this_month"]
                }catch(e){
                    return 0
                }
            case "6":
                try{
                    return accountInfo[app]["last_month"]
                }catch(e){
                    return 0
                }
            default:
                return 0
        }
    }

    const data = (index) => {
        return {
            labels: ["Modicare", "Vestige", "HHI", "ProteinWorld", "AmulyaHerbal"],
            datasets: [
              {
                label: "Performance",
                data: [getData(index, "modicare"),getData(index, "vestige"),getData(index, "hhi"),getData(index, "proteinWorld"),getData(index, "amulyaHerbal")],
              },
            ],
          };
    }

    if (window.Chart) {
        parseOptions(Chart, chartOptions());
      }

    const toggleNavs = (e, index) => {
        e.preventDefault();
        setActiveNav(index);
        setChartExample1Data("data" + index);
      };

    const toggleOptions = (e, index) => {
        setAccountType(e.target.value)
    }

    return (
        <>
            <Card className="bg-gradient-default shadow">
              <CardHeader className="bg-transparent">
                <Row className="align-items-center">
                  <div className="col">
                    <h6 className="text-uppercase text-light ls-1 mb-1">
                      Overview
                    </h6>
                    <h2 className="text-info mb-0">TeamsBuilders Performance</h2>
                  </div>
                  <div className="col">
                    <Nav className="justify-content-end" pills>
                      <NavItem>
                        <Input type="select" value={accountType} name="by_option" onChange={toggleOptions} className="form-control">
                            <option value={1}>Total</option>
                            <option value={2}>Today</option>
                            <option value={3}>Yesterday</option>
                            <option value={4}>This Week</option>
                            <option value={5}>This Month</option>
                            <option value={6}>Last Month</option>
                        </Input>
                      </NavItem>
                    </Nav>
                  </div>
                </Row>
              </CardHeader>
              <CardBody>
                {/* Chart */}
                <div className="chart">
                  <Line
                    data={data(accountType)}
                    options={chartExample5.options}
                    getDatasetAtEvent={(e) => console.log(e)}
                  />
                </div>
              </CardBody>
            </Card>
        </>
    )
}

export default UserAppChart;