import { useState, useEffect } from "react";
// node.js library that concatenates classes (strings)
import classnames from "classnames";
// javascipt plugin for creating charts
import Chart from "chart.js";
// react plugin used to create charts
import { Line, Bar } from "react-chartjs-2";
// reactstrap components
import {
  Table,
  Media,
  Card,
  CardHeader,
  CardBody,
  Container,
  Row,
  Col,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
} from "reactstrap";

// core components
import {
  chartOptions,
  parseOptions,
  chartExample1,
  chartExample2,
} from "variables/charts.js";

import { useSelector } from "react-redux";
import { GetReferralCode, ReferredUsers } from "../../components/ApiCall/userApi";
import { toast } from "react-toastify";

import Header from "components/Headers/NewHeader.js";


const Referrals = (props) => {
    const user = useSelector((state) => state.user)
    const [referralCode, setReferralCode] = useState(null);
    const [referredUsers, setReferralUsers] = useState([]);
    const imageStyle = {
        height: "120px",
        width: "190px"
    }

    useEffect(() => {
        GetReferralCode(user.user.user_id, user.user.token).then((res)=>{
            if(res.status === 200){
                setReferralCode(res.data.referral_code)
                ReferredUsers(res.data.referral_code,user.user.token).then((result)=>{
                    setReferralUsers(result.data)
                }).catch((err)=>{
                    toast.error(err.message,{
                        position:toast.POSITION.TOP_CENTER
                    });
                })
            }else{
                toast.error(res.data.message,{
                    position:toast.POSITION.TOP_CENTER
                });
            }
        })
    },[])

  return (
    <>
      {/* {activationData.activation ? (<PayHeader/>) : (<Header/>)} */}
      
      {/* Page content */}
      <Header activate={false}/>
      <Container className="mt-6 " fluid>
              <Row>
                  <Col lg={6} md={8}>
                      <Card className="bg-secondary shadow border-0">
                          <CardHeader className="bg-transparent pb-2">
                              <div className="text-muted text-center mt-2 mb-2">
                                  <h2>Refer Your Friends</h2>
                                  <img src={require("../../assets/img/brand/refer.jpg")} class="img-fluid img-thumbnail" style={imageStyle} />
                              </div>
                          </CardHeader>
                          <CardBody className="px-lg-5 py-lg-5">
                              <div className="text-center text-muted mb-4">
                                  <h3>Refer Your friends and get benefits</h3> 
                                  <small>Refer and earn 3% of total income generated by your friend's as reward. Be your own Boss.</small>
                              </div>
                              <Form role="form">
                                  <FormGroup>
                                      <InputGroup className="input-group-alternative mb-3">
                                          <InputGroupAddon addonType="prepend">
                                              <InputGroupText>
                                                  <i className="ni ni-trophy" />
                                              </InputGroupText>
                                          </InputGroupAddon>
                                          <Input
                                              placeholder="My Referral Code"
                                              type="text"
                                              autoComplete="new-referral"
                                              value={referralCode}
                                              readOnly="true"
                                          />
                                      </InputGroup>
                                  </FormGroup>
                                    <Row className="text-center">
                                        <Col md={4}>
                                            <a href={"https://api.whatsapp.com/send?text=https%3A%2F%2Fteamsbuilders.com%2F%23%2Fauth%2Fpre-register?referral_code="+ referralCode} target="_blank" className="whatsapp"><i className="fab fa-whatsapp fa-3x text-success"></i></a> 
                                        </Col>
                                        <Col md={4}>
                                            <a href={"https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fteamsbuilders.com%2F%23%2Fauth%2Fpre-register?referral_code="+ referralCode} target="_blank" className="facebook"><i className="fab fa-facebook-f fa-3x"></i></a> 
                                        </Col>
                                        <Col md={4}>
                                            <a href={"https://twitter.com/intent/tweet?url=https%3A%2F%2Fteamsbuilders.com%2F%23%2Fauth%2Fpre-register?referral_code="+ referralCode} target="_blank" className="twitter"><i className="fab fa-twitter fa-3x text-info"></i></a> 
                                        </Col>
                                    </Row>
                              </Form>
                          </CardBody>
                      </Card>
                  </Col>
                  <Col lg={6} md={4}>
                    <Card>
                        <CardHeader>
                            <div className="text-muted text-center mt-2 mb-2">
                                <h2>User Referred by me</h2>
                            </div>
                        </CardHeader>
                        <CardBody>
                            <Table className="align-items-center table-flush" responsive>
                                <thead className="thead-light">
                                    <tr>
                                        <th scope="col">Referee</th>
                                        <th scope="col">Mob no</th>
                                        <th scope="col">Joined on</th>
                                        <th scope="col">Last Month Income</th>
                                    </tr>
                                </thead>
                                <tbody>
                                { referredUsers.length === 0 ? <div className="text-danger text-center">No referred users</div> : <></>}
                                { referredUsers.map((user)=>(
                                    <>
                                    <tr>
                                        <th scope="row">
                                            <Media className="align-items-center">
                                                <a
                                                className="avatar rounded-circle mr-3"
                                                href="#pablo"
                                                onClick={(e) => e.preventDefault()}
                                                >
                                                <img
                                                    alt="User DP"
                                                    src={user.referred.profile_picture||"https://i.pinimg.com/736x/8b/16/7a/8b167af653c2399dd93b952a48740620.jpg"}
                                                />
                                                </a>
                                                <Media>
                                                <span className="mb-0 text-sm">
                                                    {user.referred.first_name} {user.referred.last_name}
                                                </span>
                                                </Media>
                                            </Media>
                                        </th>
                                        <td>{user.referred.mob}</td>
                                        <td>
                                        {user.created_at}
                                        </td>
                                        <td>
                                        &#x20b9; 0.00
                                        </td>
                                    </tr>
                                    </>
                                    )) }
                                </tbody>
                            </Table>
                        </CardBody>
                    </Card>
                  </Col>
              </Row>
      </Container>
    </>
  );
};

export default Referrals;
