import React from 'react'
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col,
  } from "reactstrap";

import { useSelector, useDispatch } from 'react-redux'
import { decrement, increment } from '../reducers/index'
import { useState, useEffect } from 'react';


const Counter = () => {

  const [ facts, setFacts ] = useState([{info:"true",source:"google"}]);
  const [ listening, setListening ] = useState(false);
  const [event, setEvent] = useState(false);

  useEffect( () => {
    if (!listening) {
      const events = new EventSource('http://localhost:8081/stream');
      events.onmessage = (event) => {
        setEvent(true);
        const parsedData = JSON.parse(event.data);
        console.log("parseData", parsedData);
        setFacts(
          (facts) => 
            
            facts.concat(parsedData)
            
          
        );
      };
      events.onerror = () => {
        console.log("error")
        setEvent(false);
      }
      setListening(true);
    }
  }, [listening, facts]);

    const count = useSelector((state) => state.counter.value)
    const dispatch = useDispatch()
    return (
        <>
        <Row>
        <Col lg="5" md="7">
            <Card className="bg-light shadow border-0">
            {!event ? <p className="text-danger">not connected</p> : <p className="text-success">Connected</p>}
              
              <CardBody className="px-lg-5 py-lg-5">
                <div className="text-center text-muted mb-4">
                  <h1>Welcome to TeamsBuilders</h1>
                  <h2>Hiii</h2>
                </div>
                <div>
                <button
                aria-label="Increment value"
                onClick={() => dispatch(increment())}
                >
                Increment
                </button>
                <span>{count}</span>
                <button
                aria-label="Decrement value"
                onClick={() => dispatch(decrement())}
                >
                Decrement
                </button>
            </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
          <Row>
          <table className="stats-table">
            <thead>
              <tr>
                <th>Fact</th>
                <th>Source</th>
              </tr>
            </thead>
            <tbody>
              {
                facts.map((fact, i) =>
                  <tr key={i}>
                    <td>{fact.info}</td>
                    <td>{fact.source}</td>
                  </tr>
                )
              }
            </tbody>
            </table>
          </Row>
        </>
    );
}

export default Counter;