import { createSlice } from '@reduxjs/toolkit'

export const ActivationSlice = createSlice({
    name: "ActivationReducer",
    initialState: {
        activation:false,
        res: null,
        modicare: {},
        vestige: {},
        hhi: {},
        proteinWorld: {},
        amulyaHerbal: {}
    },
    reducers: {
        checkActivate: (state, action) => {
            state.activation = action.payload.activation;
            state.res = action.payload.res;
        },
        userActivationData: (state, action) => {
            state.modicare = action.payload.modicare;
            state.vestige = action.payload.vestige;
            state.hhi = action.payload.hhi;
            state.proteinWorld = action.payload.proteinWorld;
            state.amulyaHerbal = action.payload.amulyaHerbal;
        },
    }
});


export const { checkActivate, userActivationData } = ActivationSlice.actions;
export const selectActivationData = (state) => state.res;
export default ActivationSlice.reducer;