import { useState, useEffect } from "react";
// node.js library that concatenates classes (strings)
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Input,
  Nav,
  Progress,
  Table,
  Container,
  Row,
  Col,
  Badge
} from "reactstrap";

import {toast} from "react-toastify";
import { useSelector, useDispatch } from "react-redux";
import PayHeader from "components/Headers/NewHeader.js";
import BankForm from "./BankDetailsForm";
import UPIPAYTMForm from "./Upi_PaytmForm";
import { UserAccountFullDetails, GetuserActiveAccountDetails } from "../../components/ApiCall/AccountApi";
import EarningTable from "./PaymentEarningTable";
import {AddBankAccount, UPIPAYTMAddForm} from "./BankAccountModal";
import { logout } from "../../reducers/userReducer";
import { useHistory } from "react-router-dom";

const UserPayment = (props) => {
  const user = useSelector((state) => state.user)
  const [userBankDetails, setBankDetails]= useState({});
  const [userAccount, setUserAccount] = useState(
    {
        "status": "ok",
        "total": 0.0,
        "today": 0.0,
        "yesterday": 0.0,
        "this_week": 0.0,
        "this_month": 0.0,
        "last_month": 0.0,
        "modicare": {
            "status": true,
            "total": 0.0,
            "today": 0.0,
            "yesterday": 0.0,
            "this_week": 0.0,
            "this_month": 0.0,
            "last_month": 0.0
        },
        "vestige": {
            "status": true,
            "total": 0.0,
            "today": 0.0,
            "yesterday": 0.0,
            "this_week": 0.0,
            "this_month": 0.0,
            "last_month": 0.0
        },
        "hhi": {
            "status": true,
            "total": 0.0,
            "today": 0.0,
            "yesterday": 0.0,
            "this_week": 0.0,
            "this_month": 0.0,
            "last_month": 0.0
        },
        "proteinWorld": {
            "status": true,
            "total": 0.0,
            "today": 0.0,
            "yesterday": 0.0,
            "this_week": 0.0,
            "this_month": 0.0,
            "last_month": 0.0
        },
        "amulyaHerbal": {
            "status": true,
            "total": 0.0,
            "today": 0.0,
            "yesterday": 0.0,
            "this_week": 0.0,
            "this_month": 0.0,
            "last_month": 0.0
        }
    }
  )

    const dispatch = useDispatch()
    const history = useHistory();

    useEffect(() => {
        getUserAccountDetails();
        getUserBankDetails();
    },[])

    const getUserBankDetails = () => {
        GetuserActiveAccountDetails(user.userData.id, user.user.token).then((res)=>{
            if(res.status == 200){
                setBankDetails(res.data.account_details);
            }else if(res.status == 401){
                UnAuthorizeLogout()
            }
            else{
                toast.error(res.data.message,{
                    position:toast.POSITION.TOP_CENTER
                });
            }
        }).catch((err)=>{
            toast.error(err.message,{
                position:toast.POSITION.TOP_CENTER
            });
        })
    }

    const getUserAccountDetails = () => {
        UserAccountFullDetails(user.userData.userId,user.user.token).then((res)=>{
            if(res.status == 200){
                setUserAccount(res.data)
            }else if(res.status == 401){
                UnAuthorizeLogout()
            }
            else{
                toast.error(res.data.message,{
                    position:toast.POSITION.TOP_CENTER
                });
            }
        }).catch((err)=>{
            toast.error(err.message,{
                position:toast.POSITION.TOP_CENTER
            });
        })
    }

    const currencyStandard = (number) => {
        console.log(number)
        if(number === undefined || number === null){
            return "0.00"
        }
        return number.toFixed(2)
    }

    const UnAuthorizeLogout = () => {
        dispatch(logout());
        // history.push("/")
    }

  return (
    <>
      <PayHeader />
      {/* Page content */}
      <Container className="mt-5 mx-auto" fluid>
          <div>
              <Row>
                  <Col>
                    <Card>
                        <CardBody>
                            <div className="container">
                                <strong>Total Earnings</strong>
                                <p><b>&#x20b9; {currencyStandard(userAccount.total)}</b></p>
                            </div>
                        </CardBody>
                    </Card>
                  </Col>
              </Row>
              <Row className="pt-3">
                  <Col>
                    <Card>
                        <CardHeader>
                            <h3>Earning Details</h3>
                        </CardHeader>
                        <CardBody>
                            <EarningTable account={userAccount}/>
                        </CardBody>
                    </Card>
                  </Col>
              </Row>
              <Row className="pt-3">
                  <Col lg="8">
                    <Card>
                        <CardHeader>
                            <Row>
                                <Col>
                                    <h3>Bank Account Details</h3>
                                </Col>
                                <Col className="text-end">
                                    <AddBankAccount user={user} updateAccount={setBankDetails} unauth={UnAuthorizeLogout}  />
                                </Col>
                            </Row>
                        </CardHeader>
                        <CardBody>
                            <BankForm user={user.userData} bankDetails={userBankDetails?.bank_account} />
                        </CardBody>
                    </Card>
                  </Col>
                  <Col lg="4">
                    <Row>
                        <Col>
                            <Card>
                                <CardHeader>
                                    <Row>
                                        <Col>
                                            <h3>UPI ID</h3>
                                        </Col>
                                        <Col>
                                            <UPIPAYTMAddForm show={"upi"} user={user} updateAccount={setBankDetails} unauth={UnAuthorizeLogout} />
                                        </Col>
                                    </Row>
                                </CardHeader>
                                <CardBody>
                                    <UPIPAYTMForm show={"upi"} user={user.userData} upi={userBankDetails?.upi_id} />
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                    <Row className="mt-3">
                        <Col>
                            <Card>
                                <CardHeader>
                                    <Row>
                                        <Col>
                                            <h3>Paytm Wallet</h3>
                                        </Col>
                                        <Col>
                                            <UPIPAYTMAddForm show={"paytm"} user={user} updateAccount={setBankDetails} unauth={UnAuthorizeLogout} />
                                        </Col>
                                    </Row>
                                </CardHeader>
                                <CardBody>
                                    <UPIPAYTMForm show={"paytm"} user={user.userData} paytm={userBankDetails?.paytm_wallet}/>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                  </Col>
              </Row>
          </div>
      </Container>
    </>
  );
};

export default UserPayment;
