import HomeNavbar from "../../components/Headers/HomeHeader";
import {
    Container,
} from "reactstrap";

import Footer from "../../components/Footers/HomeFooter";


const AboutUs = () => {
    return (
        <>
            <HomeNavbar activate={true} />
            <Container className="mt-5 pt-7 pb-5">
                <h1>About Us</h1>
                <hr/>
                <ul className="aboutUs">
                    <li>Teams Builders is a money earning website where everyone can earn money easily by giving 3 to 4 hours of time daily.</li>
                    <li>Yes, we have 50000 of customers from all India have taken benefits by working 3 to 4 hours daily.</li>
                    <li>The amount of money you can generate from our system is depends how much bills you are generate from our application.</li>
                    <li>Our aim and goal are to remove unemployment from India by giving different types of work from job.</li>
                    <li>We have already given job opportunity to 50000+ peoples from India who are getting monthly 5000 Rs by doing our works.</li>
                    <li>Our developers' teams are working so hard to give you a best money generating platform all over India.</li>
                    <li>Now a days Our company giving work from home basis job only for mobile users. The requirements are very low to get start work with us.</li>
                </ul>
                <div className="mt-2">
                    <h3><u>What types of work TeamsBuilders Provide?</u></h3>
                    <ol className="aboutUs">
                        <li>You can start work with us if you have a mobile phone and active internet connection with you.</li>
                        <li>we give 5 applications to our user to generate bill in that application to get paid.</li>
                    </ol>
                </div>
                <div className="mt-2">
                    <h3><u>When We Started?</u></h3>
                    <ol className="aboutUs">
                        <li>We have started our journey from 2019 with data entry work from home jobs and now we are providing very simple jobs like generating bill.</li>
                    </ol>
                </div>
            </Container>
            <Footer />
        </>
    )
}

export default AboutUs