import { useState, useEffect } from "react";
// node.js library that concatenates classes (strings)
import classnames from "classnames";
// javascipt plugin for creating charts
import Chart from "chart.js";
// react plugin used to create charts
import { Line, Bar } from "react-chartjs-2";
// reactstrap components
import {
  Container,
} from "reactstrap";


import { useSelector, useDispatch } from "react-redux";
import { activationApi, CheckActivity } from "../../components/ApiCall/userApi";

import Header from "components/Headers/NewHeader.js";
import Activate from "../Supplement/Activate";
import Activated from "../Supplement/Activated";
import NewLanding from "../Dashboard/newLanding"
import { checkActivate, userActivationData } from "../../reducers/ActivationReducer";

const UserLanding = (props) => {
  const user = useSelector((state) => state.user )
  const user_id = useSelector((state) => state.user.user.user_id)
  const activationData = useSelector((state) => state.activation)


  const dispatch = useDispatch()
  
  useEffect(() => {
    activationApi(user_id).then((res)=>{
      if(res.status === 200){
        if(!res.data.activation){
          CheckActivity(user_id, "allapps").then((response)=>{
            if(response.status === 200){
              dispatch(userActivationData({
                modicare: response.data.modicare,
                vestige: response.data.vestige,
                hhi: response.data.hhi,
                proteinWorld: response.data.proteinWorld,
                amulyaHerbal: response.data.amulyaHerbal
              }))
            }
          })
        }
        dispatch(checkActivate({
          activation:res.data.activation,
          res:res.data
        }))
      }else if(res.status === 202){
        dispatch(checkActivate({
          activation:res.data.activation,
          res:res.data
        }))
      }
    }).catch((err)=>{
      console.error(err)
    })
  },[])
  
  return (
    <div className="landing-main">      
      {/* Page content */}
      <Header activate={activationData.activation}/>
      <Container className="mt-5 justify-content-center" fluid>
        {activationData.activation ? <NewLanding user={user} activation={activationData} /> : <Activate user={user} activation={activationData.res}/> }
      </Container>
    </div>
  );
};

export default UserLanding;
