import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Input,
    Nav,
    Progress,
    Table,
    Container,
    Row,
    Col,
  } from "reactstrap";

import { useState, useEffect } from "react";
import { toast } from "react-toastify";
import {AddUpiID, AddPaytmWallet} from "../../components/ApiCall/AccountApi";

const UPIPAYTMForm  = (props) => {
    const [u_pData, setUpPaytmiId] = useState({
        upiId: "",
        paytm: ""
    });

    useEffect(() => {
        if(props.upi){
            updateUpiData(props.upi.upi_id);
        }
        if(props.paytm){
            paytmUpiData(props.paytm.paytm_wallet)
        }
    },[props])


    const updateUpiData = (data) => {
        setUpPaytmiId(prevValue=>({
            ...prevValue,
            upiId: data
        }))
    }

    const paytmUpiData = (data) => {
        setUpPaytmiId(prevValue=>({
            ...prevValue,
            paytm: data
        }))
    }

    const inputEvent = (e) => {
        setUpPaytmiId((prevValue)=>{
            return {
              ...prevValue,
              [e.target.name]:  e.target.value
            }
        })
    }

    const AddUPIData = async (e) => {
        e.preventDefault()
        e.target.upi_btn.innerHTML = "Adding UPI...";
        e.target.upi_btn.disabled = true;

        let res = await AddUpiID(props.user.id,u_pData.upiId);
        if(res.status === 201){
            e.target.upi_btn.innerHTML = "Add UPI";
            e.target.upi_btn.disabled = false;
            toast.success("UPI ID Added Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
        }else if(res.status == 401){
            toast.error("UnAuthorized, You are logged out.",{
                position:toast.POSITION.TOP_CENTER
            });
            props.unauth()
        }
        else{
            e.target.upi_btn.innerHTML = "Add UPI";
            e.target.upi_btn.disabled = false;
            toast.error("Something went wrong! Please try again later.",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    const AddPaytmData = async (e) => {
        e.preventDefault()
        e.target.paytm_btn.innerHTML = "Adding Paytm...";
        e.target.paytm_btn.disabled = true;

        let res = await AddPaytmWallet(props.user.id,u_pData.paytm);
        if(res.status === 201){
            e.target.paytm_btn.innerHTML = "Add Paytm";
            e.target.paytm_btn.disabled = false;
            toast.success("Paytm Wallet Added Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
        }else if(res.status == 401){
            toast.error("UnAuthorized, You are logged out.",{
                position:toast.POSITION.TOP_CENTER
            });
            props.unauth()
        }
        else{
            e.target.paytm_btn.innerHTML = "Add Paytm";
            e.target.paytm_btn.disabled = false;
            toast.error("Something went wrong! Please try again later.",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    return (
        <>
        { props.show == "upi" ? <>
            <FormGroup>
                <label
                    className="form-control-label"
                    htmlFor="upi-code"
                >
                    UPI Code
                </label>
                <Input
                    className="form-control-alternative"
                    value={u_pData.upiId}
                    id="upi-code"
                    name="upiId"
                    placeholder="UPI Code"
                    type="text"
                    readOnly={true}
                />
            </FormGroup>
            
        </> : <>
            <FormGroup>
                <label
                    className="form-control-label"
                    htmlFor="paytm-wallet"
                >
                    Paytm Wallet
                </label>
                <Input
                    className="form-control-alternative"
                    value={u_pData.paytm}
                    id="paytm-wallet"
                    placeholder="Paytm Wallet"
                    name="paytm"
                    type="text"
                    readOnly={true}
                />
            </FormGroup>
        </> 
        }
       
        </>
    )
}

export default UPIPAYTMForm;