import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col,
  } from "reactstrap";

import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { existance } from '../../reducers/ExistUser';
import { UserExist, verifyUserExist, userDetail } from '../../components/ApiCall/userApi';
import { toast } from "react-toastify";
import queryString from 'query-string'

const PreRegister = (props) => {
    let history = useHistory();
    const [mob, setMob] = useState("");
    const [otp, setOtp] = useState("");
    // const [searchParams, setSearchParams] = useSearchParams();
    // searchParams.get("referral_code")

    const params = queryString.parse(props.location.search);
    

    const dispatch = useDispatch()

    const checkUser = async () => {
        console.log("leng", mob.length)
        if(!mob){
            toast.error("Please enter your mobile number",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }else if(mob.length !== 10){
            toast.error("Please enter valid mobile number",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }
        let userExist = await UserExist(mob);
        if(userExist.status === 206){
            toast.success("Otp has been sent to your mobile number",{
                position:toast.POSITION.TOP_CENTER
            });
            document.getElementById('checkBtn').classList.add("otp-inactive");
            document.getElementById('submitBtn').classList.remove("otp-inactive");
            document.getElementById('otp_form').classList.remove("otp-inactive");
        }else if(userExist.status === 200){
            toast.success("Account already exists!",{
                position:toast.POSITION.TOP_CENTER
            });
            history.push('/auth/login');
        }
        else{
            dispatch(
                existance(
                  { 
                    user_id: null,
                    user_data: {
                        mob: "+91"+mob,
                        referral_code: params?.referral_code || null,
                    },
                    user_token: null,
                    pre_register: true
                  }
                )
            )
            history.push('/auth/register');
        }
        
    }

    const verifyUser = async (e) => {
        e.preventDefault();
        if(!mob || !otp){
            toast.error("Invalid Form",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }
        let verify = await verifyUserExist(mob, otp);
        if(verify.status === 200){
            toast.success("We have your partial data! Kindly fill other fields to register.",{
                position:toast.POSITION.TOP_CENTER
            });
            let userData = await userDetail(verify.data.user_id);
            dispatch(
                existance(
                  { 
                    user_id: verify.data.user_id,
                    user_data: userData.data.data,
                    user_token: verify.data.token,
                    pre_register: true,
                    referral_code: params?.referral_code || null,
                  }
                )
              )
              history.push('/auth/register');
        }
    }

    const inputEvent = (e) => {
        if(e.target.name === "mob"){
            setMob(e.target.value);
        }else if(e.target.name === "otp"){
            setOtp(e.target.value);
        }
    }

    return (
        <>
            <Col lg="5" md="5">
                <Card className="bg-secondary shadow border-0">
                    <CardBody className="px-lg-5 py-lg-3">
                        <div className="text-center text-muted mb-4">
                            <small>Pre-Registration</small>
                        </div>
                        <Form role="form" method="post" onSubmit={verifyUser}>
                            <FormGroup className="mb-3">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-mobile-button" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Mob"
                                        type="text"
                                        name="mob"
                                        onChange={inputEvent}
                                        autoComplete="new-mob"
                                        value={mob}
                                        required
                                        maxLength="10"
                                        size="10"
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup id="otp_form" className="form-group otp-inactive">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-send" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="OTP"
                                        type="text"
                                        name="otp"
                                        id="otp"
                                        onChange={inputEvent}
                                        autoComplete="new-password"
                                        value={otp}
                                    />
                                </InputGroup>
                            </FormGroup>
                            {/* <div className="custom-control custom-control-alternative custom-checkbox">
                                <input
                                    className="custom-control-input"
                                    id=" customCheckLogin"
                                    type="checkbox"
                                />
                                <label
                                    className="custom-control-label"
                                    htmlFor=" customCheckLogin"
                                >
                                    <span className="text-muted">Remember me</span>
                                </label>
                            </div> */}
                            <div className="text-center">
                            <Button className="my-4" color="secondary" id="cancelBtn" type="button" onClick={(e) => {e.preventDefault(); history.push('/home')}}>
                                    Cancel
                                </Button>
                                <Button className="my-4" color="primary" id="checkBtn" type="button" onClick={(e) => {e.preventDefault(); checkUser();}}>
                                    Check User
                                </Button>
                                <Button className="my-4 otp-inactive" id="submitBtn" color="primary" type="submit">
                                    Submit
                                </Button>
                            </div>
                        </Form>
                    </CardBody>
                </Card>
                <Row className="mt-3">
                    <Col xs="6">
                        
                    </Col>
                    <Col className="text-right" xs="6">
                        <a
                            className="text-light"
                            href="#pablo"
                            onClick={(e) => { e.preventDefault(); history.push('/auth/login') }}
                        >
                            <small>Already have an account?</small>
                        </a>
                    </Col>
                </Row>
            </Col>
        </>
    )
}

export default PreRegister;