import { Card, CardBody, CardHeader, CardTitle, Container, Row, Col, Button } from "reactstrap";
import useCollapse from 'react-collapsed'
import ReactTooltip from 'react-tooltip';
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";



const Spans = (props) => {
    let arr = []
    if(props.day == 1){
        arr = [...Array(4).keys()];
        arr.fill(0, 0, 4).fill(1, 0, props.num);
    }else{
        arr = [...Array(3).keys()];
        arr.fill(0, 0, 3).fill(1, 0, props.num);
    }

    return (
        <>
            {
                props.day === 1 ? 
                <> 
                    {
                        arr.map((item, index)=>(
                            <>
                            {item==1 ? <span className="badge badge-success">Bill{index+1}</span> : <span className="badge badge-warning"><s>Bill{index+1}</s></span>}
                            </>
                        ))
                    }
                </>
                :
                <>
                    {
                        arr.map((item, index)=>(
                            <>
                            {item==1 ? <span className="badge badge-success">Bill{index+1}</span> : <span className="badge badge-warning"><s>Bill{index+1}</s></span>}
                            </>
                        ))
                    }
                </>
            }
        </>
    )
}

const ActivationSign = (props) => {
    return (
        <>
            { props.activate ? 
            <>
                <a data-tip="This process has completed."
                    className="text-info">
                    <i className="fa fa-check-circle text-success" aria-hidden="true"></i>
                </a>
                <ReactTooltip place="left" type="dark" effect="float"/>
                            
            </>
            :
            <>
                <a data-tip="Please complete this process"
                    className="text-info">
                    <i className="fa fa-times-circle text-danger" aria-hidden="true"></i>
                </a>
                <ReactTooltip place="left" type="dark" effect="float"/>
            </>
            }
        </>
    )
}

const Activity = (props) => {
    const activity = useSelector((state)=> state.activation[props.app.id]) || {status:"failed"};
    const { getCollapseProps, getToggleProps, isExpanded } = useCollapse()

    return (
        <>
           <div className="activity">
                <button {...getToggleProps()} className="btn btn-info">
                    {isExpanded ? 'Hide Activity' : 'Show Activity'}
                </button>
                <section {...getCollapseProps()} className="text-left pt-4">
                    {props.app.name}' Activity 
                    <a data-tip="Strike through badges suggest, the bill yet to be generated."
                    className="text-info ml-2">
                    <i className="fa fa-info-circle" aria-hidden="true"></i>
                    </a>
                    <ReactTooltip place="left" type="dark" effect="float"/>
                    <Row className="justify-content-center mt-2">
                        <Col md={8}>
                            <strong><b>Day1</b></strong> : 
                            { activity.status=="ok" ? 
                            <> { activity.result.day1_status ? <Spans day="1" num="4"/> : <Spans day="1" num={activity.result.success_bill_no}/> } </> 
                            : 
                            <Spans day="1" num="0"/>
                            }
                                                       
                        </Col>
                        <Col md={4} className="cross-icon">
                        { activity.status=="ok" ? 
                        <>
                            { activity.result.day1_status ? <ActivationSign activate={activity} /> : <ActivationSign activate={false} />}
                        </>   
                        : 
                        <ActivationSign activate={false} />
                        }
                        </Col>
                    </Row>
                    <Row className="justify-content-center mt-2">
                        <Col md={8}>
                            <strong><b>Day2</b></strong> : 
                            { activity.status=="ok" ? 
                            <> { activity.result.day1_status && activity.result.day2_status ? <Spans day="2" num="3"/> : activity.result.day1_status && !activity.result.day2_status ? <Spans day="2" num={activity.result.success_bill_no}/> : <Spans day="2" num="0"/> } </> 
                            : 
                            <Spans day="2" num="0"/>
                            }
                        </Col>
                        <Col md={4} className="cross-icon">
                            { activity.status=="ok" ? 
                            <>
                                { activity.result.day2_status && activity.result.day1_status ? <ActivationSign activate={activity} /> : <ActivationSign activate={false} />}
                            </>   
                            : 
                            <ActivationSign activate={false} />
                            }
                        </Col>
                    </Row>
                    <Row className="justify-content-center mt-2">
                        <Col md={8}>
                            <strong><b>Day3</b></strong> : 
                            { activity.status=="ok" ? 
                            <> {  activity.result.day1_status && activity.result.day2_status && activity.result.day3_status ? <Spans day="3" num="3"/> : activity.result.day1_status && activity.result.day2_status && !activity.result.day3_status ? <Spans day="3" num={activity.result.success_bill_no}/> : <Spans day="3" num="0"/> } </> 
                            : 
                            <Spans day="3" num="0"/>
                            }
                        </Col>
                        <Col md={4} className="cross-icon">
                            { activity.status=="ok" ? 
                                <>
                                    { activity.result.day3_status ? <ActivationSign activate={activity} /> : <ActivationSign activate={false} />}
                                </>   
                            : 
                            <ActivationSign activate={false} />
                            }
                        </Col>
                    </Row>
                </section>
            </div>
        </>
    )
}

export default Activity;