import axios from "axios";

export const userLogin = async (mob, password) => {
    if(!mob || !password) return;
      let data = JSON.stringify({
        "mob": "+91"+mob,
        "password": password
      });
      
      let config = {
        method: 'post',
        url: process.env.REACT_APP_TEAMSBUILDERS_URL+'api/login',
        headers: { 
          'Content-Type': 'application/json', 
        },
        data : data
      };
      try{
        return await axios(config);
      }catch(error){
          return error.response
      }
      
}

export const UserRegistration = async (userData) => {
  console.log(userData);
  let data = JSON.stringify({
    mob: "+91"+userData.signupData.mob,
    email: userData.signupData.email,
    first_name: userData.signupData.firstName,
    last_name: userData.signupData.lastName,
    dob: userData.signupData.dob,
    gender: userData.signupData.gender,
    state: userData.signupData.state,
    city: userData.signupData.city,
    pincode: userData.signupData.pincode,
    password: userData.signupData.password,
    referral_code: userData.signupData.referral_code,
  })
  let config = {
    method: 'post',
    url: process.env.REACT_APP_TEAMSBUILDERS_URL+'api/register',
    headers: { 
      'Content-Type': 'application/json', 
    },
    data : data
  };
  try{
    return await axios(config);
  }catch(error){
      return error.response
  }
}

export const UserExist = async (mob,forgotpass=false) => {
  if(!mob)return;
  let data = JSON.stringify({
    "mob": "+91"+mob,
  });

  let config = {
    method: 'post',
    url: process.env.REACT_APP_TEAMSBUILDERS_URL+`api/userexist?forgotPassword=${forgotpass}`,
    headers: { 
      'Content-Type': 'application/json', 
    },
    data : data
  };
  try{
    return await axios(config);
  }catch(error){
      return error.response
  }
}

export const verifyUserExist = async (mob, otp) => {
  if(!mob || !otp) return;
  let data = JSON.stringify({
    "mob": "+91"+mob,
    "otp": otp
  });
  let config = {
    method: 'post',
    url: process.env.REACT_APP_TEAMSBUILDERS_URL+'api/verifyOtp/',
    headers: { 
      'Content-Type': 'application/json', 
    },
    data : data
  };
  try{
    return await axios(config);
  }catch(error){
      return error.response
  }
}


export const userDetail = async (userId, token) => {
  if(!userId)return;
  let config = {
      method: 'get',
      url: process.env.REACT_APP_TEAMSBUILDERS_URL+'api/'+ userId +'/user',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `token ${token}`, 
      }
    };
    try{
      return await axios(config)
    }catch(err){
        return err.response
    }
}   

