import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Input,
    Nav,
    Progress,
    Table,
    Container,
    Row,
    Col,
  } from "reactstrap";

import {useState, useEffect} from "react";
import { toast } from "react-toastify";

const BankForm = (props) => {
    const [bankDetails, setBankDetails] = useState({
        account_number: "",
        ifsc_code: "",
        account_holder_name: "",
        bank_name: ""
    })

    useEffect(() => {
        if(props.bankDetails){
            setBankDetails(props.bankDetails)
        }
    }, [props])

    return (
        <>
            <div className="pl-lg-4">
                    <Row>
                        <Col lg="6">
                            <FormGroup>
                            <label
                                    className="form-control-label"
                                    htmlFor="bank-name"
                                >
                                    Account Number
                                </label>
                                <Input
                                    className="form-control-alternative"
                                    value={bankDetails.bank_name}
                                    id="bank-name"
                                    name="bank_name"
                                    placeholder="Bank Name"
                                    type="text"
                                    required
                                    readOnly={true}
                                />
                            </FormGroup>
                        </Col>
                        <Col lg="6">
                        <FormGroup>
                                <label
                                    className="form-control-label"
                                    htmlFor="account-name"
                                >
                                    Account Holder Name
                                </label>
                                <Input
                                    className="form-control-alternative"
                                    value={bankDetails.account_holder_name}
                                    name="account_holder_name"
                                    id="account-name"
                                    placeholder="Account Name"
                                    type="text"
                                    required
                                    readOnly={true}
                                />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg="6">
                            <FormGroup>
                                <label
                                    className="form-control-label"
                                    htmlFor="account-number"
                                >
                                    Account Number
                                </label>
                                <Input
                                    className="form-control-alternative"
                                    value={bankDetails.account_number}
                                    id="account-number"
                                    name="account_number"
                                    placeholder="Account Number"
                                    type="number"
                                    required
                                    readOnly={true}
                                />
                            </FormGroup>
                        </Col>
                        <Col lg="6">
                        <FormGroup>
                                <label
                                    className="form-control-label"
                                    htmlFor="ifsc-code"
                                >
                                    Bank IFSC Code
                                </label>
                                <Input
                                    className="form-control-alternative"
                                    value={bankDetails.ifsc_code}
                                    name="ifsc_code"
                                    id="ifsc-code"
                                    placeholder="IFSC Code"
                                    type="text"
                                    required
                                    readOnly={true}
                                />
                            </FormGroup>
                        </Col>
                    </Row>
            </div>
        </>
    )
}

export default BankForm;