import HomeNavbar from "../../components/Headers/HomeHeader";
import {
    Container,
    Row,
    Col,
    Form,
    FormGroup,
    Card,
    Input,
    Button,
    CardBody,
    CardHeader,
    CardTitle
} from "reactstrap";
import { useState } from "react";
import Footer from "../../components/Footers/HomeFooter";


const ContacUs = () => {
    const [contactForm, setContactForm] = useState({
        full_name: "",
        email: "",
        mob: "",
        description: "",
    })
 
    const inputEvent = (e) => {
        setContactForm((prevValue)=>{
          return {
            ...prevValue,
            [e.target.name]:  e.target.value
          }
        })
      }
    const SendMessage = (e) => {
        e.preventDefault()
        setContactForm(
            {
                full_name: "",
                email: "",
                mob: "",
                description: "",
            }
        )
    }
    
    return (
        <>
            <HomeNavbar activate={true} />
            <Container className="mt-5 pt-7 pb-5">
                <h1>Contact Us</h1>
                <div className="mt-3">
                    <h2 className="text-center mb-3"><u>Send us a message</u></h2>
                    <Row className="justify-content-center">
                        <Col md={6}>
                            <Card color="secondary" style={{borderRadius:"15px", }}>
                                <CardBody>
                                    <Form onSubmit={SendMessage}>
                                        <FormGroup>
                                            <Input type="text" 
                                                className="form-control"
                                                value={contactForm.full_name}
                                                onChange={inputEvent}
                                                name="full_name"
                                                placeholder="Full Name" 
                                            />
                                        </FormGroup>
                                        <FormGroup>
                                            <Input type="email" 
                                                name="email"
                                                value={contactForm.email}
                                                onChange={inputEvent}
                                                className="form-control"
                                                placeholder="Email" 
                                            />
                                        </FormGroup>
                                        <FormGroup>
                                            <Input type="number"
                                                name="mob"
                                                value={contactForm.mob}
                                                onChange={inputEvent}
                                                className="form-control"
                                                placeholder="Mobile Number" 
                                            />
                                        </FormGroup>
                                        <FormGroup>
                                            <textarea rows="8" 
                                                className="form-control" 
                                                value={contactForm.description}
                                                onChange={inputEvent}
                                                name="description"
                                                placeholder="Your message" 
                                            />
                                        </FormGroup>
                                        <Row className="justify-content-center">
                                            <FormGroup>
                                                <Button color="primary" type="submit">Submit</Button>
                                                <Button color="secondary">Cancel</Button>
                                            </FormGroup>
                                        </Row>
                                    </Form>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </Container>
            <Footer />
        </>
    )
}

export default ContacUs;