import { configureStore } from '@reduxjs/toolkit'
import counterReducer from "./reducers/index";
import userSlice from "./reducers/userReducer";
import ActivationSlice from "./reducers/ActivationReducer";
import existUserSlice from "./reducers/ExistUser";
import EventEmitterSlice from './reducers/eventEmitter';


export default configureStore({
  reducer: {
    counter: counterReducer ,
    user: userSlice,
    activation: ActivationSlice,
    userexist: existUserSlice,
    eventEmitter: EventEmitterSlice
  }
})