import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col,
  } from "reactstrap";

import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { existance } from '../../reducers/ExistUser';
import { UserExist, verifyUserExist, userDetail, resetPassword } from '../../components/ApiCall/userApi';
import { toast } from "react-toastify";


const ForgotPassword = (props) => {
    let history = useHistory();
    const [userInfo, setUserInfo] = useState({
        mob: "",
        password: "",
        cnfpassword: ""
    });
    const [otp, setOtp] = useState("");
    const [passwordValidate, setpasswordValidate] = useState(false);
    const [user_id, setUserId] = useState(null);


    const dispatch = useDispatch()

    const checkUser = async () => {
        if(!userInfo.mob){
            toast.error("Please enter your mobile number",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }
        let userExist = await UserExist(userInfo.mob, "True");
        if(userExist.status === 200){
            toast.info("OTP has been sent to your mobile number!",{
                position:toast.POSITION.TOP_CENTER
            });
            document.getElementById('checkBtn').classList.add("otp-inactive");
            document.getElementById('verifyBtn').classList.remove("otp-inactive");
            document.getElementById('otp_form').classList.remove("otp-inactive");     
        }else{
            toast.error("Invalid Mobile Number!",{
                position:toast.POSITION.TOP_CENTER
            });
        }
        
    }

    const verifyUser = async () => {
        if(!userInfo.mob || !otp){
            toast.error("Invalid Form",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }  
        let verify = await verifyUserExist(userInfo.mob, otp);
        if(verify.status === 200){
            toast.info("Now You are ready to reset your password.",{
                position:toast.POSITION.TOP_CENTER
            });
            document.getElementById('verifyBtn').classList.add("otp-inactive");
            document.getElementById('submitBtn').classList.remove("otp-inactive");
            document.getElementById('otp_form').classList.add("otp-inactive");   
            document.getElementById('password_form').classList.remove("otp-inactive");     
            document.getElementById('cnfpassword_form').classList.remove("otp-inactive");   
            setUserId(verify.data.user_id)
        }
    }

    const changePassword = async () => {
        if(!passwordValidate) {
            toast.error("Password and ConfirmPassword didn't match!",{
                position:toast.POSITION.TOP_CENTER
            });
            return;
        }
        let res = await resetPassword(user_id, userInfo.password)
        if(res.status === 200){
            toast.success(res.data.message,{
                position:toast.POSITION.TOP_CENTER
            });
            history.push('/auth/login')
        }else{
            console.log("res",res);
            toast.error("something went wrong",{
                position:toast.POSITION.TOP_CENTER
            });
        }

    }

    const checkPassword = (e) => {
        let pass = document.getElementById("password")    
        setTimeout(() => {
          if(userInfo.password === userInfo.cnfpassword){
            e.target.className = "form-control";
            pass.className = "form-control";
            setpasswordValidate(true);
          }else{
            e.target.className += " text-danger"
            pass.className += " text-danger";
            setpasswordValidate(false);
          }
        },100)
      }

    const inputEvent = (e) => {
        if(e.target.name === "otp"){
            setOtp(e.target.value)
        }else{
            setUserInfo((prevValue)=>{
                return {
                  ...prevValue,
                  [e.target.name]:  e.target.value
                }
            })
        }
    }

    return (
        <>
            <Col lg="5" md="5">
                <Card className="bg-secondary shadow border-0">
                    <CardBody className="px-lg-5 py-lg-3">
                        <div className="text-center text-muted mb-4">
                            <small>Forgot Password</small>
                        </div>
                        <Form role="form" method="post" onSubmit={(e)=>{e.preventDefault(); changePassword()}}>
                            <FormGroup className="mb-3">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-mobile-button" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Mob"
                                        type="text"
                                        name="mob"
                                        onChange={inputEvent}
                                        autoComplete="new-mob"
                                        value={userInfo.mob}
                                        required
                                        maxLength="10"
                                        size="10"
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup id="password_form" className="form-group otp-inactive">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                        <i className="ni ni-lock-circle-open" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Password"
                                        type="password"
                                        name="password"
                                        id="password"
                                        onChange={inputEvent}
                                        autoComplete="new-password"
                                        value={userInfo.password}
                                        required
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup id="cnfpassword_form" className="form-group otp-inactive">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                        <i className="ni ni-lock-circle-open" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Confirm Password"
                                        type="text"
                                        name="cnfpassword"
                                        onChange={inputEvent}
                                        onBlur={checkPassword}
                                        autoComplete="new-cnfpassword"
                                        value={userInfo.cnfpassword}
                                        required
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup id="otp_form" className="form-group otp-inactive">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-send" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="OTP"
                                        type="text"
                                        name="otp"
                                        id="otp"
                                        onChange={inputEvent}
                                        autoComplete="new-password"
                                        value={otp}
                                    />
                                </InputGroup>
                            </FormGroup>
                            {/* <div className="custom-control custom-control-alternative custom-checkbox">
                                <input
                                    className="custom-control-input"
                                    id=" customCheckLogin"
                                    type="checkbox"
                                />
                                <label
                                    className="custom-control-label"
                                    htmlFor=" customCheckLogin"
                                >
                                    <span className="text-muted">Remember me</span>
                                </label>
                            </div> */}
                            <div className="text-center">
                            <Button className="my-4" color="secondary" id="cancelBtn" type="button" onClick={(e) => {e.preventDefault(); history.push('/home')}}>
                                    Cancel
                                </Button>
                                <Button className="my-4" color="primary" id="checkBtn" type="button" onClick={(e) => {e.preventDefault(); checkUser();}}>
                                    Submit
                                </Button>
                                <Button className="my-4 otp-inactive" color="primary" id="verifyBtn" type="button" onClick={(e) => {e.preventDefault(); verifyUser();}}>
                                    Verify User
                                </Button>
                                <Button className="my-4 otp-inactive" id="submitBtn" color="primary" type="submit">
                                    Change Password
                                </Button>
                            </div>
                        </Form>
                    </CardBody>
                </Card>
                <Row className="mt-3">
                    <Col xs="6">
                        <a
                            className="text-light"
                            href="#pablo"
                            onClick={(e) => {e.preventDefault();history.push('/auth/login')}}
                        >
                            <small>Already have account?</small>
                        </a>
                    </Col>
                    <Col className="text-right" xs="6">
                        <a
                            className="text-light"
                            href="#pablo"
                            onClick={(e) => { e.preventDefault(); history.push('/auth/register') }}
                        >
                            <small>Create new account</small>
                        </a>
                    </Col>
                </Row>
            </Col>
        </>
    )
}

export default ForgotPassword;