import { Badge, Modal, ModalHeader, ModalBody, ModalFooter, Button, FormGroup, Input, Row, Col,} from 'reactstrap';
import { useState, useEffect } from 'react';
import { AddUserBankAccount, getUserbankAccounts, getUserUpis, getUserPaytms, PostUserActiveAccount } from "../../components/ApiCall/AccountApi";
import {AddUpiID, AddPaytmWallet} from "../../components/ApiCall/AccountApi";
import { toast } from "react-toastify";

export const AddBankAccount = (props) => {

    const [bankDetails, setBankDetails] = useState({
        account_number:"",
        confirm_account_number:"",
        ifsc_code:"",
        account_holder_name: "",
        bank_name: ""
    })

    const [bankAccounts, setBankAccounts] = useState([])

    useEffect(() => {
        getUserbankAccounts(props.user.userData.id, props.user.user.token).then((accounts) => {
            if(accounts.status == 200){
                setBankAccounts(accounts.data)
            }else if(accounts.status == 401){
                toast.error("UnAuthorized, You are logged out.",{
                    position:toast.POSITION.TOP_CENTER
                });
                props.unauth()
            }
            else{
                toast.error(accounts.data.message,{
                    position:toast.POSITION.TOP_CENTER
                });
            }
        }).catch((error) => {
            toast.error(error.message,{
                position:toast.POSITION.TOP_CENTER
            });
        })
    },[])

    const [accountNumValidation, setAccountNumValidation] = useState(false);

    const AddbankDetails = async (e) => {
        e.preventDefault()

        e.target.bank_form_btn.innerHTML = "Adding Bank...";
        e.target.bank_form_btn.disabled = true;

        if(!accountNumValidation){
            toast.error("Account Number and Confirm Account Number didn't match!",{
              position:toast.POSITION.TOP_CENTER
            });
            return;
          }
        let res = await AddUserBankAccount(props.user.id, 
            bankDetails.account_number, 
            bankDetails.ifsc_code, 
            bankDetails.account_holder_name,
            bankDetails.bank_name
        );
        if(res.status === 201){
            e.target.bank_form_btn.innerHTML = "Add Bank Details";
            e.target.bank_form_btn.disabled = false;
            toast.success("Bank Account Added Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
            setBankAccounts(prevValue=>[...prevValue, res.data])
            toggle()
        }else if(res.status == 401){
            toast.error("UnAuthorized, You are logged out.",{
                position:toast.POSITION.TOP_CENTER
            });
            props.unauth()
        }else{
            e.target.bank_form_btn.innerHTML = "Add Bank Details";
            e.target.bank_form_btn.disabled = false;
            toast.error("Something went wrong! Please try again later.",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    const inputEvent = (e) => {
        setBankDetails((prevValue)=>{
          return {
            ...prevValue,
            [e.target.name]:  e.target.value
          }
        })
    }

    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);

    const closeBtn = (
        <button className="close" onClick={toggle} type="button">
          &times;
        </button>
      );


      const checkAccountNumber = (e) => {
        let account_number = document.getElementById("account-number")    
        setTimeout(() => {
          if(bankDetails.account_number === bankDetails.confirm_account_number){
            e.target.className = "form-control";
            account_number.className = "form-control";
            setAccountNumValidation(true);
          }else{
            e.target.className += " text-danger"
            account_number.className += " text-danger";
            setAccountNumValidation(false);
          }
        },1000)
      }


    const AddToActiveAccounts = async (e) => {
        e.preventDefault();
        let bank_id = e.target.bank_account.value;
        let res = await PostUserActiveAccount("bankDetails",bank_id, props.user.user.token);
        if(res.status == 200 || res.status === 201){
            toast.success("User Account updated Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
            props.updateAccount(res.data.data);
            toggle()
        }else if(res.status == 401){
            toast.error("UnAuthorized, You are logged out.",{
                position:toast.POSITION.TOP_CENTER
            });
            props.unauth()
        }else{
            toast.error("Something went wrong, Please try again later!",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    return (
        <>
            <Badge color="info" onClick={toggle} style={{cursor: "pointer"}}>
                Add Bank Account
            </Badge>
            <Modal isOpen={modal} backdrop={false} centered={true} toggle={toggle}>
                <ModalHeader toggle={toggle} close={closeBtn}>
                    Add Bank Account
                </ModalHeader>
                <ModalBody>
                    {bankAccounts.length > 0 ? <>
                    <h4 className="p-3 text-center">Your {bankAccounts.length} Bank account/s is already with us, choose a default or add a new account</h4>
                    <form onSubmit={AddToActiveAccounts}>
                        <Row>
                            <Col>
                                <Input type="select" 
                                    name="bank_account"
                                    className="form-control-alternative"
                                >
                                <option disabled>Select Bank Account</option>
                                {bankAccounts.map((account) =>(
                                    <option value={account.id}>{account.account_number}</option>
                                ))}

                                </Input>   
                            </Col>
                            <Col>
                                <Button color="info">Make it Default</Button>
                            </Col>
                        </Row>
                    </form>
                    </>
                    : <></>}
                    <hr></hr>
                    <Row>
                        <div className="pl-lg-4">
                            <form onSubmit={AddbankDetails}>
                                <Row>
                                    <Col lg="6">
                                        <FormGroup>
                                        <label
                                                className="form-control-label"
                                                htmlFor="bank-name"
                                            >
                                                Account Number
                                            </label>
                                            <Input
                                                className="form-control-alternative"
                                                value={bankDetails.bank_name}
                                                onChange={inputEvent}
                                                id="bank-name"
                                                name="bank_name"
                                                placeholder="Bank Name"
                                                type="text"
                                                required
                                            />
                                        </FormGroup>
                                    </Col>
                                    <Col lg="6">
                                    <FormGroup>
                                            <label
                                                className="form-control-label"
                                                htmlFor="account-name"
                                            >
                                                Account Holder Name
                                            </label>
                                            <Input
                                                className="form-control-alternative"
                                                value={bankDetails.account_holder_name}
                                                onChange={inputEvent}
                                                name="account_holder_name"
                                                id="account-name"
                                                placeholder="Account Name"
                                                type="text"
                                                required
                                            />
                                        </FormGroup>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col lg="6">
                                        <FormGroup>
                                            <label
                                                className="form-control-label"
                                                htmlFor="account-number"
                                            >
                                                Account Number
                                            </label>
                                            <Input
                                                className="form-control-alternative"
                                                value={bankDetails.account_number}
                                                onChange={inputEvent}
                                                id="account-number"
                                                name="account_number"
                                                placeholder="Account Number"
                                                type="number"
                                                required
                                            />
                                        </FormGroup>
                                    </Col>
                                    <Col lg="6">
                                        <FormGroup>
                                            <label
                                                className="form-control-label"
                                                htmlFor="cnf-account-number"
                                            >
                                                Confirm Account Number
                                            </label>
                                            <Input
                                                className="form-control-alternative"
                                                value={bankDetails.confirm_account_number}
                                                onChange={inputEvent}
                                                onBlur={checkAccountNumber}
                                                name="confirm_account_number"
                                                id="cnf-account-number"
                                                placeholder="Account Number"
                                                type="number"
                                                required
                                            />
                                        </FormGroup>
                                    </Col>
                                </Row>
                                <Row>
                                <Col lg="6">
                                    <FormGroup>
                                            <label
                                                className="form-control-label"
                                                htmlFor="ifsc-code"
                                            >
                                                Bank IFSC Code
                                            </label>
                                            <Input
                                                className="form-control-alternative"
                                                value={bankDetails.ifsc_code}
                                                onChange={inputEvent}
                                                name="ifsc_code"
                                                id="ifsc-code"
                                                placeholder="IFSC Code"
                                                type="text"
                                                required
                                            />
                                        </FormGroup>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col className="text-end">
                                        <FormGroup>
                                            <div className="text-end">
                                                <Button className="my-4" name="cancel_btn" color="secondary" type="button" onClick={toggle}>Cancel</Button>
                                                <Button className="my-4" name="bank_form_btn" color="primary" type="submit">Add Bank Details</Button>
                                            </div>
                                        </FormGroup>
                                    </Col>
                                </Row>
                            </form>
                        </div>
                    </Row>
                </ModalBody>
            </Modal>
        </>
    )
}

export const UPIPAYTMAddForm = (props) => {
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);

    const closeBtn = (
        <button className="close" onClick={toggle} type="button">
          &times;
        </button>
      );

    const [u_pData, setUpPaytmiId] = useState({
        upiId:"",
        paytm:""
    });

    const [upiIds, setUpiIds] = useState([])

    const [paytms, setPaytms] = useState([])

    useEffect(() => {
        getUPIPaytmdatas()
    },[])

    const getUPIPaytmdatas = () => {
        if(props.show === "upi"){
            getUserUpis(props.user.userData.id, props.user.user.token).then((upis)=>{
                if(upis.status === 200){
                    setUpiIds(upis.data)
                }else if(upis.status === 401){
                    props.unauth()
                }
            })
        }else{
            getUserPaytms(props.user.userData.id, props.user.user.token).then((paytm)=>{
                if(paytm.status === 200){
                    setPaytms(paytm.data)
                }else if(paytm.status === 401){
                    props.unauth()
                }
            })
        }
        
    }
    
    const inputEvent = (e) => {
        setUpPaytmiId((prevValue)=>{
            return {
              ...prevValue,
              [e.target.name]:  e.target.value
            }
        })
    }

    const AddUPIData = async (e) => {
        e.preventDefault()
        e.target.upi_btn.innerHTML = "Adding UPI...";
        e.target.upi_btn.disabled = true;

        let res = await AddUpiID(props.user.userData.id, u_pData.upiId, props.user.user.token);
        if(res.status === 201){
            e.target.upi_btn.innerHTML = "Add UPI";
            e.target.upi_btn.disabled = false;
            toast.success("UPI ID Added Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
            setUpiIds(prevValue=>[...prevValue,res.data])
            toggle()
        }else{
            e.target.upi_btn.innerHTML = "Add UPI";
            e.target.upi_btn.disabled = false;
            toast.error("Something went wrong! Please try again later.",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    const AddPaytmData = async (e) => {
        e.preventDefault()
        e.target.paytm_btn.innerHTML = "Adding Paytm...";
        e.target.paytm_btn.disabled = true;

        let res = await AddPaytmWallet(props.user.userData.id, u_pData.paytm, props.user.user.token);
        if(res.status === 201){
            e.target.paytm_btn.innerHTML = "Add Paytm";
            e.target.paytm_btn.disabled = false;
            toast.success("Paytm Wallet Added Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
            setPaytms(prevValue=>[...prevValue,res.data])
            toggle()
        }else{
            e.target.paytm_btn.innerHTML = "Add Paytm";
            e.target.paytm_btn.disabled = false;
            toast.error("Something went wrong! Please try again later.",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    const AddToActiveAccounts = async (e, type) => {
        e.preventDefault();
        let bank_id = e.target.upi_paytm.value;
        let res = await PostUserActiveAccount(type,bank_id, props.user.user.token);
        if(res.status == 200 || res.status === 201){
            toast.success("User Account updated Successfully!",{
                position:toast.POSITION.TOP_CENTER
            });
            props.updateAccount(res.data.data);
            toggle()
        }else{
            toast.error("Something went wrong, Please try again later!",{
                position:toast.POSITION.TOP_CENTER
            });
        }
    }

    return (
        <>
            <Badge color="info" onClick={toggle} style={{cursor: "pointer"}}>
                {props.show === "upi" ? 
                "Add UPI"
                 : 
                "Add Paytm Wallet"
                }
                
            </Badge>
            <Modal isOpen={modal} centered={true} backdrop={false} toggle={toggle}>
            <ModalHeader toggle={toggle}>{props.show === "upi" ? 
                "Add UPI"
                 : 
                "Add Paytm Wallet"
                }
            </ModalHeader>
            <ModalBody>
                { props.show == "upi" ? <>
                    {upiIds.length > 0 ? <>
                    <h4 className="p-3 text-center">Your {upiIds.length} UPI ID/s is already with us, choose a default or add a new UPI ID</h4>
                    <form onSubmit={(e)=>AddToActiveAccounts(e,"upi")}>
                        <Row>
                            <Col>
                                <Input type="select" 
                                    name="upi_paytm"
                                    className="form-control-alternative"
                                >
                                <option >Select UPI ID</option>
                                {upiIds.map((upi) =>(
                                    <option value={upi.id}>{upi.upi_id}</option>
                                ))}

                                </Input>   
                            </Col>
                            <Col>
                                <Button color="info">Make it Default</Button>
                            </Col>
                        </Row>
                    </form>
                    <hr />
                    </> 
                    : <></>}
                    <form onSubmit={AddUPIData}>
                        <FormGroup>
                            <label
                                className="form-control-label"
                                htmlFor="upi-code"
                            >
                                UPI Code
                            </label>
                            <Input
                                className="form-control-alternative"
                                value={u_pData.upiId}
                                id="upi-code"
                                name="upiId"
                                onChange={inputEvent}
                                placeholder="UPI Code"
                                type="text"
                            />
                        </FormGroup>
                        <FormGroup>
                            <Button className="my-4" name="cancel_btn" color="secondary" type="button" onClick={toggle}>Cancel</Button>
                            <Button className="my-4" name="upi_btn" color="primary" type="submit">Add UPI</Button>
                        </FormGroup>
                    </form>
                    
                </> : <>
                {paytms.length > 0 ? <>
                    <h4 className="p-3 text-center">Your {paytms.length} Paytm wallet/s is already with us, choose a default or add a new paytm wallet</h4>
                    <form onSubmit={(e)=>AddToActiveAccounts(e,"paytm")}>
                        <Row>
                            <Col>
                                <Input type="select" 
                                    name="upi_paytm"
                                    className="form-control-alternative"
                                >
                                <option >Select Paytm Wallet</option>
                                {paytms.map((paytm) =>(
                                    <option value={paytm.id}>{paytm.paytm_wallet}</option>
                                ))}

                                </Input>   
                            </Col>
                            <Col>
                                <Button color="info">Make it Default</Button>
                            </Col>
                        </Row>
                    </form>
                    <hr />
                    </>:<></>}
                    <form onSubmit={AddPaytmData}>
                        <FormGroup>
                            <label
                                className="form-control-label"
                                htmlFor="paytm-wallet"
                            >
                                Paytm Wallet
                            </label>
                            <Input
                                className="form-control-alternative"
                                value={u_pData.paytm}
                                id="paytm-wallet"
                                onChange={inputEvent}
                                placeholder="Paytm Wallet"
                                name="paytm"
                                type="text"
                            />
                        </FormGroup>
                        <FormGroup>
                            <Button className="my-4" name="cancel_btn" color="secondary" type="button" onClick={toggle}>Cancel</Button>
                            <Button className="my-4" name="paytm_btn" color="primary" type="submit">Add Paytm</Button>
                        </FormGroup>
                    </form>
                </> 
                }
       
            </ModalBody>
        </Modal>
        </>
    )

}