// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
} from "reactstrap";
import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import { existance } from '../../reducers/ExistUser';
import { UserRegistration, verifyUserExist, ReferralCodeValidation } from "../../components/ApiCall/userApi";


const Register = () => {
  let history = useHistory()
  const dispatch = useDispatch()
  
  const userExist = useSelector((state) => state.userexist)
  const [signupData, setSignUpData] = useState({
    mob: userExist?.userData?.mob.split("+91")[1] || "",
    email: userExist?.userData?.email || "",
    firstName: userExist?.userData?.first_name || "",
    lastName: userExist?.userData?.last_name || "",
    state: "",
    city: "",
    pincode: "",
    password: "",
    cnfpassword: "",
    dob: "",
    gender: "",
    otp: "",
    count: "",
    referral_code: userExist?.userData?.referral_code || ""
  });
  const [passwordValidate, setpasswordValidate] = useState(false);
  const [validReferral, setValidReferral] = useState(false);

  useEffect(()=>{
    if(!userExist.preRegister){
      history.push('/auth/pre-register')
    }
    else if(userExist?.userData?.referral_code){
      checkReferral(userExist?.userData?.referral_code);
    }
  },[])

  const checkPassword = (e) => {
    let pass = document.getElementById("password")    
    setTimeout(() => {
      if(signupData.password === signupData.cnfpassword){
        e.target.className = "form-control";
        pass.className = "form-control";
        setpasswordValidate(true);
      }else{
        e.target.className += " text-danger"
        pass.className += " text-danger";
        setpasswordValidate(false);
      }
    },1000)
  }

  const inputEvent = (e) => {
    setSignUpData((prevValue)=>{
      return {
        ...prevValue,
        [e.target.name]:  e.target.value
      }
    })
  }

  const signupFunc = async (e) => {
    e.preventDefault()
    if(!passwordValidate){
      toast.error("Password and ConfirmPassword didn't match!",{
        position:toast.POSITION.TOP_CENTER
      });
      return;
    }
    let register = await UserRegistration({signupData})
    if(register.status === 201){
      toast.success(register.data.message,{
        position:toast.POSITION.TOP_CENTER
      });
      document.getElementById('otpSubmitBtn').classList.remove("otp-inactive");
      document.getElementById('submitBtn').classList.add("otp-inactive");
      document.getElementById('otp_form').classList.remove("otp-inactive");
    }else if(register.status === 400){
      toast.error(register.data.message, {
        position:toast.POSITION.TOP_CENTER
      })
    }
  }

  const verifySignup = async (e) => {
    if(!signupData.mob || !signupData.otp){
        toast.error("Invalid Form",{
            position:toast.POSITION.TOP_CENTER
        });
        return;
    }
    let verify = await verifyUserExist(signupData.mob, signupData.otp);
    if(verify.status === 200){
      toast.success("You have successfully registered!",{
          position:toast.POSITION.TOP_CENTER
      });
      CancelRegistration();
      history.push('/auth/login')
    }
}


const checkReferral = async (ref_code) => {
  if(!ref_code)return;
  let res = await ReferralCodeValidation(ref_code);
  if(res.status === 200){
    setValidReferral(true);
  }else{
    setSignUpData((prevValue)=>{
      return {
        ...prevValue,
        referral_code:  ""
      }
    })
    setValidReferral(false);
    toast.error("Invalid Referral Code",{
      position:toast.POSITION.TOP_CENTER
  });
  }
}

const CancelRegistration = () => {
    dispatch(
      existance(
        {
          user_id: null,
          user_data: null,
          user_token: null,
          pre_register: false
        }
      )
    )
}

  return (
    <>
      <Col lg="6" md="8">
        <Card className="bg-secondary shadow border-0">
          <CardBody className="px-lg-5 py-lg-5">
            <div className="text-center text-muted mb-4">
              <small>Sign up with credentials</small>
            </div>
            <Form role="form" method="post" onSubmit={signupFunc}>
            <FormGroup>
              <Row>
                <Col md={6}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-mobile-button" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="10 digit Mob-number"
                      type="text"
                      name="mob"
                      onChange={inputEvent}
                      autoComplete="new-mob"
                      value={signupData.mob}
                      required
                      maxLength="10"
                      size="10"
                    />
                  </InputGroup>
                </Col>
                <Col md={6}>
                  <InputGroup className="input-group-alternative">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="ni ni-email-83" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      placeholder="Email"
                      type="email"
                      name="email"
                      onChange={inputEvent}
                      autoComplete="new-email"
                      value={signupData.email}
                      required
                    />
                  </InputGroup>
                </Col>
              </Row>
              </FormGroup>
              <FormGroup>
                <Row>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-hat-3" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input placeholder="firstName" onChange={inputEvent} type="text" name="firstName" value={signupData.firstName}  autoComplete="new-firstName"  required/>
                    </InputGroup>
                  </Col>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-hat-3" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input placeholder="lastName" onChange={inputEvent} type="text" name="lastName" value={signupData.lastName}  autoComplete="new-lastName" required/>
                    </InputGroup>
                  </Col>
                </Row>
              </FormGroup>
              <FormGroup>
                <Row>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-calendar-grid-58" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="DOB"
                        type="date"
                        name="dob"
                        onChange={inputEvent}
                        autoComplete="new-dob"
                        value={signupData.dob}
                        required
                      />
                    </InputGroup>
                  </Col>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-circle-08" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="Gender"
                        type="select"
                        name="gender"
                        onChange={inputEvent}
                        autoComplete="new-gender"
                        value={signupData.gender}
                        required
                      >
                        <option value="">Select Gender</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                        <option value="Other">Other</option>
                      </Input>
                    </InputGroup>
                  </Col>
                </Row>
              </FormGroup>
              <FormGroup>
                <Row>
                  <Col md={4}>
                      <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-shop" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="State"
                        type="text"
                        onChange={inputEvent}
                        name="state"
                        autoComplete="new-state"
                        value={signupData.state}
                        required
                      />
                    </InputGroup>
                  </Col>
                  <Col md={4}>
                  <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-shop" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                       placeholder="City"
                       type="text"
                       onChange={inputEvent}
                       name="city"
                       autoComplete="new-city"
                       value={signupData.city}
                       required
                      />
                    </InputGroup>
                  </Col>
                  <Col md={4}>
                  <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-shop" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="Pincode"
                        type="text"
                        onChange={inputEvent}
                        name="pincode"
                        autoComplete="new-pincode"
                        value={signupData.pincode}
                        required
                      />
                    </InputGroup>
                  </Col>
                </Row>
              </FormGroup>
              <FormGroup>
                <Row>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-lock-circle-open" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="Password"
                        type="password"
                        name="password"
                        id="password"
                        onChange={inputEvent}
                        autoComplete="new-password"
                        value={signupData.password}
                        required
                      />
                    </InputGroup>
                  </Col>
                  <Col md={6}>
                    <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="ni ni-lock-circle-open" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        placeholder="Confirm Password"
                        type="text"
                        name="cnfpassword"
                        onChange={inputEvent}
                        onBlur={checkPassword}
                        autoComplete="new-cnfpassword"
                        value={signupData.cnfpassword}
                        required
                      />
                    </InputGroup>
                  </Col>
                </Row>
              </FormGroup>
              <FormGroup>
                <Row>
                  <Col md={8}>
                    <InputGroup className="input-group-alternative">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="ni ni-trophy" />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          placeholder="Referral Code"
                          type="text"
                          name="referral_code"
                          onChange={inputEvent}
                          onBlur={(e)=>checkReferral(e.target.value)}
                          autoComplete="new-referral_code"
                          value={signupData.referral_code}
                        />
                        {
                        validReferral ? 
                          <InputGroupAddon addonType="append">
                          <InputGroupText>
                            <i className="ni ni-check-bold text-success" />
                          </InputGroupText>
                        </InputGroupAddon>
                        :<></>
                      }
                    </InputGroup>
                  </Col>
                </Row>
              </FormGroup>
              {/* <div className="text-muted font-italic">
                <small>
                  password strength:{" "}
                  <span className="text-success font-weight-700">strong</span>
                </small>
              </div> */}
              <Row className="my-4">
                <Col xs="12">
                  <div className="custom-control custom-control-alternative custom-checkbox">
                    <input
                      className="custom-control-input"
                      id="customCheckRegister"
                      type="checkbox"
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="customCheckRegister"
                    >
                      <span className="text-muted">
                        I agree with the{" "}
                        <a href="#pablo" onClick={(e) => e.preventDefault()}>
                          Privacy Policy
                        </a>
                      </span>
                    </label>
                  </div>
                </Col>
              </Row>
              <FormGroup id="otp_form" className="form-group otp-inactive">
                  <InputGroup className="input-group-alternative">
                      <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                              <i className="ni ni-send" />
                          </InputGroupText>
                      </InputGroupAddon>
                      <Input
                          placeholder="OTP"
                          type="text"
                          name="otp"
                          id="otp"
                          onChange={inputEvent}
                          autoComplete="new-otp"
                          value={signupData.otp}
                      />
                  </InputGroup>
              </FormGroup>
              <div className="text-center">
              <Button className="mt-4" color="secondary" type="button" onClick={(e) => {e.preventDefault(); CancelRegistration(); history.push('home')}} >
                  Cancel
                </Button>
                <Button className="mt-4" color="primary" id="submitBtn" type="submit">
                  SignUp
                </Button>
                <Button className="mt-4 otp-inactive" color="primary" id="otpSubmitBtn" type="button" onClick={(e) => {e.preventDefault(); verifySignup();}} >
                  Verify SignUp
                </Button>
              </div>
            </Form>
          </CardBody>
        </Card>
        <Row className="mt-3">
        <Col xs="6">
            
          </Col>
          <Col className="text-right" xs="6">
            <a
              className="text-light"
              href="#pablo"
              onClick={(e) => {e.preventDefault();history.push('/auth/login')}}
            >
              <small>Already have an account?</small>
            </a>
          </Col>
        </Row>
      </Col>
    </>
  );
};

export default Register;
